.reviews {

   .container {
      @extend %container;
   }

   &__inner {
      padding-top: 40px;
   }

   &__title {
      text-align: center;
      margin-bottom: 15px;
      @include text(26px, "Montserrat-400", 39px, $textColor);
   }

   &__title-colon {
      display: none;

      @include media(sm) {
         display: inline-block;
         text-align: left;
      }
   }

   &__item {
      padding: 30px 0 35px;
      border: solid #C7C7C7;
      border-width: 0 0 .5px;
   }

   &__wrap-1,
   &__wrap-2 {
      display: flex;
   }

   &__wrap-1 {
      flex-direction: column;
      margin-bottom: 16px;
   }

   &__wrap-2 {
      margin-bottom: 20px;
   }

   &__img {
      width: 85px;
      height: 85px;
      margin-right: 18px;

      @include media(sm) {
         width: 100px;
         height: 100px;
      }
   }

   &__block-name {
      position: relative;
      margin-top: 5px;

      &::after {
         @extend %pseudo;
         bottom: 8px;
         left: 0;
         width: 118px;
         height: 17px;
         background: url("./../images/stars.png") center no-repeat;
      }
   }

   &__text-1 {
      margin-bottom: 5px;
      @include text(16px, "Montserrat-500", 20px, $textColor);
   }

   &__text-2 {
      @include text(14px, "Montserrat-400", 21px, $textColor);
   }

   &__text-3 {
      @include text(14px, "Montserrat-400", 21px, $textColorLight);
   }

   &__paragraph {
      margin-bottom: 20px;
   }

   &__vector {
      position: relative;
      padding-left: 20px;

      &::before {
         @extend %pseudo;
         top: 5px;
         left: 0;
         width: 15px;
         height: 12px;
         background: url("./../images/reviews-img-vector.png") center no-repeat;
      }
   }

   &__checker {
      display: none;
   }

   &__limited-block {
      max-height: 800px;
      overflow: hidden;
      margin-bottom: 5px;
      z-index: 3;
   }

   &__wrap-label {
      display: flex;
      justify-content: center;
      padding-top: 5px;
   }

   &__button {
      position: relative;
      text-decoration: none;
      @include text(17px, "Montserrat-500", 25px, $linkColor);
      cursor: pointer;

      &::before {
         @extend %pseudo;
         width: 11px;
         height: 6px;
         top: 10px;
         left: -20px;
         background: url("./../images/Arrow.png") no-repeat;
      }

      &::after {
         @extend %pseudo;
         width: 11px;
         height: 6px;
         top: 10px;
         right: -20px;
         background: url("./../images/Arrow.png") no-repeat;
      }
   }

   &__checker:checked~&__limited-block {
      max-height: none;      
   }
}